.widget{
    color: whitesmoke;
    display: flex;
    justify-content: space-between;
    flex: 1;
    padding: 10px;
    -webkit-box-shadow: 3px 3px 5px 0px rgba(105, 105, 105, 1);
    -moz-box-shadow: 3px 3px 5px 0px rgba(105, 105, 105, 1);
    box-shadow: 3px 3px 5px 0px rgba(105, 105, 105, 1);
    border-radius: 10px;
    height: 100px;

    .left,
    .right{
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .title{
            font-weight: bold;
            font-size: 14px;
            color: rgb(184, 184, 184);
        }
        .counter{
            font-size: 28px;
            font-weight: 300;
        }
        .link{
            width: max-content;
            font-size: 12px;
            border-bottom: 1px solid grey;
        }
        .percentage{
            display: flex;
            align-items: center;
            font-size: 14px;

            &.positive{
                color: green;
            }
            &.negative{
                color: red;
            }
        }
        .icon{
            font-size: 18px;
            padding: 5px;
            border-radius: 5px;
            align-self: flex-end;
            
        }
    }
}