.chart {
    flex: 4;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
    padding: 10px;
    border-radius: 10px;
    color: rgb(138, 138, 138);

    .title {
        margin-bottom: 20px;
    }

    .chartGrid {
        stroke: rgba(255, 212, 118, 0.192);
    }
}